<template>
  <div id="add-job" class="w3-content w3-padding-large">
    <form v-on:submit.prevent="addJob" id="add-item-form" action="">
      <label>Tulostettava kappale</label>
      <input type="text" v-model="part" placeholder="default" required>

      <label>Tulostaja</label>
      <input type="text" v-model="name" placeholder="default" required>

      <label>Tulostusaika (h)</label>
      <input type="number" v-model="time" placeholder="0" min="0" required>

      <label>Kiireisyys</label><br>
      <input type="radio" value="high" v-model="priority">
      <label for="high">Kiireinen</label><br>
      <input type="radio" value="low" v-model="priority">
      <label for="low">Ei kiireinen</label>

      <input type="submit" value="Lisää" class="w3-button w3-green w3-hover-green w3-hover-text-black w3-section">
      <router-link to="/" class="w3-button w3-block w3-grey w3-round w3-text-white w3-hover-grey w3-hover-text-black">Takaisin</router-link>
    </form>
  </div>
</template>

<script>
  import db from "./firebaseInit"
  export default {
    name: 'add-job',
    data () {
      return {
        part: null,
        name: null,
        time: 0,
        priority: "high"
      }
    },
    methods: {
      addJob() {
        db.collection("print_jobs").add({
          name: this.name,
          part: this.part,
          print_time: this.time,
          priority: this.priority
        })
        .then(docRef => this.$router.push("/"))
        .catch(err => console.error("Error adding new job: ", err))
      }
    }
  }
</script>
    
<style scoped>
label {
  font-size: 1.2em;
}

input[type="text"], input[type="submit"], input[type="number"] {
  display: block;
  width: 100%;
  padding: 16px;
  margin-bottom: 8px;
  border-radius: 8px;
}
</style>
    


