<template>
  <div id="dashboard">
    <div class="w3-row">
      <div class="w3-twothird w3-padding">
        <div class="w3-container">
          <ul id="print-job-list" class="w3-ul">
            <li v-for="job in jobs" class="print-job" v-bind:class="{ 'priority-high': (job.priority === 'high'), 'priority-low': (job.priority !== 'high') }">
              <button class="w3-button w3-circle w3-white w3-hover-green w3-large w3-right" v-on:click="deleteJob(job)">&#10003;</button>
              <span class="w3-xlarge">{{ job.part }}</span><br>
              Tulostaja: {{ job.name }} <br>
              Aika: {{ job.time }}h
            </li>
          </ul>
          <router-link to="/add" id="add-button" class="w3-circle w3-card w3-button w3-green w3-xlarge w3-hover-green w3-hover-text-black">+</router-link>
        </div>
      </div>
      <div class="w3-third w3-padding">
        <div class="w3-container w3-white w3-round w3-padding w3-large">
          <p><u>Aika</u></p>
          <p>Kaikki: {{ timeEstimate }}h</p>
          <p>Kiireiset: {{ priorityEstimate }}h</p>
          <p><u>Käytettävissä</u></p>
          <p>{{ estimatedDate.toLocaleString("fi", {  weekday: "long", hour: "2-digit", minute: "2-digit" }) }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import db from "./firebaseInit"
  export default {
    name: 'dashboard',
    data () {
      return {
        jobs: [],
        timeEstimate: 0,
        priorityEstimate: 0,
        estimatedDate: new Date()
      }
    },
    created () {
      this.fetchJobs()
    },
    methods: {
      comparePriorities(jobA, jobB) {
        if (jobA.priority === "high") return -1
        if (jobA.priority !== "high") return 1
        return 0
      },
      fetchJobs() {
        this.jobs = []
        db.collection("print_jobs").get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            const data = {
              "id": doc.id,
              "name": doc.data().name,
              "part": doc.data().part,
              "time": doc.data().print_time,
              "priority": doc.data().priority
            }
            this.jobs.push(data)
          })
          this.jobs.sort(this.comparePriorities)
          this.timeEstimate = this.jobs.reduce((sum, cur) => sum + Number(cur.time), 0)
          this.priorityEstimate = this.jobs.reduce((sum, cur) => {
            if (cur.priority === "high") return sum + Number(cur.time)
            return sum + 0
          }, 0)
          this.checkEstimatedDate()
        })
      },
      deleteJob(clickedJob) {
        if (confirm("Oletko varma?")) {
          console.log(clickedJob.id)
          db.collection("print_jobs").where("part", "==", clickedJob.part).where("name", "==", clickedJob.name).where("print_time", "==", clickedJob.time).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
              this.fetchJobs()
            })
          })
        }
      },
      checkEstimatedDate() {
        let curDate = new Date()
        curDate.setHours(curDate.getHours() + Number(this.timeEstimate))
        this.estimatedDate = curDate;
      }
    }
  }
</script>
    
<style scoped>
li.print-job {
  margin: 8px 0;
  padding: 16px;
  color: white !important;
  border: 0 !important;
  border-radius: 8px;
}

li.priority-high {
  background: rgb(217, 83, 79) !important;
}

li.priority-low {
  background: rgb(240, 173, 78) !important;
}

#add-button {
  position: fixed;
  bottom: 25px;
  right: 25px;
}
</style>
    


